/* src/Dashboard/WeatherDetails.css */

.weather-details-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  color: #fff;
  text-align: center;
  padding: 20px;
  transition: background 0.5s ease;
}

.weather-header h1 {
  font-size: 2.5em;
  margin: 0;
  text-transform: uppercase;
}

.weather-header .date {
  font-size: 1.1em;
  font-weight: 300;
}

.weather-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.weather-icon img {
  width: 150px;
  height: 150px;
}

.weather-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.temperature {
  font-size: 3em;
  font-weight: bold;
}

.weather-details p {
  font-size: 1.2em;
  margin: 5px 0;
}

.footer {
  font-size: 0.9em;
  opacity: 0.7;
  margin-top: 20px;
}

/* Backgrounds for different weather conditions */
.clear-bg {
  background: linear-gradient(120deg, #a0d0ff, #f0e68c);
}

.cloud-bg {
  background: linear-gradient(120deg, #bdc3c7, #2c3e50);
}

.rain-bg {
  background: linear-gradient(120deg, #3a3a3a, #3498db);
}

.default-bg {
  background: linear-gradient(120deg, #4682b4, #5f9ea0);
}

/* Loading and Error */
.loading, .error {
  font-size: 1.5em;
  color: #333;
}
.search-bar {
  display: flex;
  justify-content: center;
  padding: 10px;
  background-color: #fff;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1000;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.search-bar input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 5px;
}

.search-bar button {
  padding: 8px 12px;
  border: none;
  background-color: #3498db;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}

.search-bar button:hover {
  background-color: #2980b9;
}
